.form-container{
    border: 2px solid black;
    width: 25%;
    margin: 30px auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.login-page-h2{
    text-align: center;
    margin: 0.5rem auto;
}

.labels{
    font-size: 18px;
    font-weight: 600;
    margin: 0.3rem 0;
    position: relative;
}

.inputs{
    height: 30px;
    width: 95%;
    padding: 0 0 0 5px;
    border: 2px solid black;
    margin-top: 10px;
}

.inputs__button-icons{
    position: absolute;
    bottom: 2px;
    right: 30px;
    background-color: white;
    border: none;
    cursor: pointer;
}

.inputs__password-icons{
    height: 22px;
    width: 25px;
}

.btns-sign-ins{
    font-size: 14px;
    background-color: black;
    border: 1px solid white;
    color: white;
    padding: 0.5rem;
    height: 40px;
    width: 96%;
    margin: 0 0 10px 0;
    cursor: pointer;

    transition-duration: 400ms;
}

.btns-sign-ins:hover{
    transform: scale(1.02);
    background-color: white;
    border: 1px solid black;
    color: black;
}

.link-signup{
    text-decoration: none;
}

.post-login {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;

    .post-login__btn {
        font-size: 16px;
        background-color: black;
        border: 2px solid white;
        color: white;
        padding: 0.5rem;
        align-self: center;
        margin: 20px;
        height: 3rem;
        width: 200px;
        cursor: pointer;
        transition-duration: 400ms;
    }

    .post-login__btn:hover {
        transform: scale(1.02);
        background-color: white;
        border: 1px solid black;
        color: black;
    }
}